.card{
    margin: 10px 70px;
    padding: 10px;
    border-radius: 3px;
    width: 300px;
    display: flex;
    box-shadow: 9px 9px 9px -3px rgba(0,0,0,0.32);
    -webkit-box-shadow: 9px 9px 9px -3px rgba(0,0,0,0.32);
    -moz-box-shadow: 9px 9px 9px -3px rgba(0,0,0,0.32);
}

img{
    width: 5rem;
}

.flex{
    display: flex;
}